.contact-container {
    text-indent: 50px;
    text-align: justify;
    padding: 10px
}

.contact-container span {
    font-size: 20px;
    margin-right: 50px;
    color: blueviolet;
    font-family: Verdana, sans-serif;
    font-weight: bold;

}

.contact-img {
    width: 100%;
    height: 120px;
}

.contact-img img {
    width: 100px;
    height: 100px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.contact-form {
    /* border: #4CAF50 solid; */
    position: absolute;
}
.contact-form div{
    top: 50%;
    left: 50%;
    display: block;
    padding-bottom: 5px;
    /* border: #f44445 solid; */
    justify-content: center;
    align-items: center;
}

button {
    width: 30%;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    border: none;
    border-radius: 4px;
}