.container-products {
    margin: 0 auto;
    padding: 10px 20px;
    max-width: 1300px;
    display: flex;

}

.bloc-card {
    position: relative;
    border: 5px solid;
    margin: 10px;
}

.bloc-card img {
    width: 100%;
    height: 80%;
    object-fit: cover;
    object-position: center;
}

.bloc-infos {
    width: 300px;
    height: 500px;
    display: block;
    
}

.bloc-infos p{
    font-size: 25px;
}
.bloc-infos form label{
    margin: 20px 0px;
    padding-right: 5px;
    font-size: 20px;
}

.bloc-infos form input{
    width: 75px;
}
.bloc-infos form button{
    padding: 12px 10px;
    font-size: 18px;
    margin: 20px 0px 20px;
}