.nav-top {
    display: flex;
    padding: 0px 20px;
    background: #333;
    max-width: 1300px;
    box-shadow: 0 10px 20px rgba(19, 19, 19, 0.2);
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;  
    margin: 80px auto 50px;
}


.nav-top a {
    font-size: 22px;
    text-decoration: none;
    color: #f1f1f1;
    padding: 20px 20px;
    
}
