.floating-cart {
    position: absolute;
    top: 15px;
    right: 15px;
    display: flex;
    padding: 10px 20px;
}

.floating-cart img{
    width: 50px;
}

.floating-cart span{
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: 1px solid black;
    justify-content: center;
    align-items: center;
    display: flex;
    font-size: 20px;
    font-weight: bolder;
    background: #f1f1f1;
    color: #222;
}