.container-products{
    margin: 0 auto;
    padding: 10px 20px;
    justify-content: center;
    align-items: center;
    max-width: 1300px;
    display: flex;
    flex-wrap: wrap;
}

.bloc-card {
    position: relative;
    display: flex;
    border: 5px solid;
    width: 300px;
    height: 500px;
    margin: 10px;
}
.bloc-card img {
    width: 100%;
    height: 80%;
    object-fit: cover; 
    object-position: center;   
}