.heading-cart {
    margin-bottom: 10px;
}

.cart-list li {

    border: 1px solid;
    background: #f1f1f1;
    display: flex;
    align-items: center;

}

.cart-list img {
    width: 50px;
    padding: 20px;
}

.bloc-infos {
    width: 250px;
    height: 10%;
    font-size: 15px;
}

.bloc-infos h4 {
    font-size: 22px;
}

.bloc-input {
    margin-left: auto;
    margin-right: 10px;
    display: inherit;
    
}
.bloc-input label {
    margin-right: 10px;
    font-size: 20px;
  }
.btn-cart {
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 10px 15px;
    font-size: 18px;
}